import React from 'react'
import {Button,Row,Col,Card} from 'antd'
import './index.css'
class Son extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      name: '爸爸没打我'
    }
    this.sendMes = this.sendMes.bind(this)
  }
  sendMes=()=>{
    var mes = '卧槽 儿子来信了'
    this.props.onChange(mes)
  }
  render(){
    return(
      // sendMes()
      <div className='erzi'>
        <Button type="primary" onClick = {this.sendMes}>给爸爸写信</Button>
        <p>{this.props.msg||'我是猪'}</p>
      </div>
    )
  }
}

class Fscom extends React.Component{
  constructor(props){
    super(props);
    this.state={
      content: '等儿子发信',
    }
    this.hitson = this.hitson.bind(this)
  }
  handleChildChange = (val)=>{
    console.log(val)
    this.setState({
        content: val,
        erziMes:''
    })
  }
  hitson = ()=>{
    console.log('asd')
    this.setState({
      content: '我去打儿子',
      erziMes: '爸爸要来打我'
    })
  }
  render(){
    return(
      <div className='baba'>
        <Son onChange={this.handleChildChange} msg={this.state.erziMes}/>
          Father
        <p>{this.state.content}</p>
        <Button type="primary" onClick = {this.hitson}>打儿子</Button>
      </div>
    )
  }
}
export default Fscom



